@mixin desktop {
    @media (max-width: 992px) {
        @content
    }
}

@mixin pad {
    @media (max-width: 768px) {
        @content
    }
}

@mixin mobile_lg {
    @media (max-width: 414px) {
        @content
    }
}

@mixin mobile {
    @media (max-width: 375px) {
        @content
    }
}

@mixin mobile_s {
    @media (max-width: 320px) {
        @content
    }
}